<template>
	<view class="background fix-padding">
		<view class="details_list">
			<view class="details_item flex_between">
				<view>
					<view class="details_item_list">
						<view class="title">订单单号</view>
						<view class="value">{{form.order_number}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">客户姓名</view>
						<view class="value">{{form.name}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">订货日期</view>
						<view class="value">{{form.order_date}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">交货日期</view>
						<view class="value">{{form.delivery_date}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">是否加急</view>
						<view class="value" v-if="form.is_urgent == 2">否</view>
						<view class="value" style="color: #FF3F25;" v-if="form.is_urgent == 1">是</view>
					</view>
					<view class="details_item_list">
						<view class="title">成交金额</view>
						<view class="value">{{form.transaction_price}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">已收金额</view>
						<view class="value">{{form.received_amount}}</view>
					</view>
					<view class="details_item_list">
						<view class="title">是否加急</view>
						<view class="value" v-if="form.is_urgent == 2">否</view>
						<view class="value" style="color: #FF3F25;" v-if="form.is_urgent == 1">是</view>
					</view>
					<view class="details_item_list">
						<view class="title">生产状态</view>
						<view class="value" style="color: #B9B9B9 ;" v-if="form.order_state == 1">待排尺</view>
						<view class="value" style="color: #25D3FF;" v-if="form.order_state == 2">已排尺</view>
						<view class="value" style="color: #FF3F25;" v-if="form.order_state == 3">待审核</view>
						<view class="value" style="color: #FF3F25;" v-if="form.order_state == 4">待生产</view>
						<view class="value" style="color: #FC9011;" v-if="form.order_state == 5">生产中</view>
						<view class="value" style="color: #34BCB5 ;" v-if="form.order_state == 6">已完成</view>
					</view>
					<view class="details_item_list">
						<view class="title">备注内容</view>
						<view class="value">{{form.remarks}}</view>
					</view>
				</view>
			</view>
		</view>
		<view class="details_note">
			<view class="title" style="width: 122rpx;">排尺图片</view>
			<view class="remarkImage">
				<view class="item_image" v-for="(item,index) in remarkImage" :key='index'>
					<image class="image" :src="item" @click="previewImage(index)" mode="aspectFill"></image>
				</view>
			</view>
		</view>
		<view class="container fix-bottom">
			<button class="btn-submit" @click="submit_shenhe" v-if="form.order_state == 3">审核</button>
			<view class="flex" v-if="form.order_state == 4">
				<button class="btn-submit" style="width: 330rpx;" @click="submit" >改为生产中</button>
				<button class="btn-submit" style="width: 330rpx; background-color: #FF3F25;" @click="submit_shenhe" >返审</button>
			</view>
			<button class="btn-submit" @click="submit" v-if="form.order_state == 5">改为已完成</button>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				id:'',
				form:{},
				remarkImage: [],
				show:false,
				received_amount:'',
			}
		},
		onLoad(option) {
			console.log('option',option)
			if(option.data) {
				this.id = JSON.parse(decodeURIComponent(option.data))
				this.listInterface()
			}
			
		},
		methods: {
			// 图片放大
			previewImage(i) {
				wx.previewImage({
					current: this.remarkImage[i],
					urls: this.remarkImage
				})
			},
			// 接口列表
			listInterface() {
				this.http({
				    url: this.api.order_info,
				    method: "POST",
				    data: {
				        uid: this.common.get("uid"),
				        id:this.id,
				    },
				    success: res => {
						this.form = res
						this.received_amount = this.form.received_amount
						this.remarkImage = res.amount_image
				    }
				})
			},
			// 关闭弹窗
			closeModal() {
				console.log(1111)
				this.show = false
			},
			submit_shenhe() {
				uni.showModal({
					title: '提示',
					content: '确定要修改当前状态吗？',
					success: res => {
						if(res.confirm) { 
							this.http({
							    url: this.api.order_check,
							    method: "POST",
							    data: {
							        uid: this.common.get("uid"),
							        id:this.id,
									order_state:this.form.order_state==4?2:4
							    },
							    success: res => {
									uni.showToast({
										title:'修改成功',
										icon:'none',
										duration: 2000
									})
									this.listInterface()
							    }
							})
						}
						
					},
					fail: () => {},
					complete: () => {}
				});
			},
			// 已收金额
			submit_mon() {
				this.http({
				    url: this.api.up_received,
				    method: "POST",
				    data: {
				        uid: this.common.get("uid"),
				        id:this.id,
						received_amount:this.received_amount
				    },
				    success: res => {
						uni.showToast({
							title:'修改成功',
							icon:'none',
							duration: 2000
						})
						this.show = false
						this.listInterface()
				    }
				})
			},
			submit() {
				uni.showModal({
					title: '提示',
					content: '确定要修改当前状态吗？',
					success: res => {
						if(res.confirm) { 
							this.http({
							    url: this.api.up_order_state,
							    method: "POST",
							    data: {
							        uid: this.common.get("uid"),
							        id:this.id,
									order_state:this.form.order_state==4?5:6
							    },
							    success: res => {
									uni.showToast({
										title:'修改成功',
										icon:'none',
										duration: 2000
									})
									this.listInterface()
							    }
							})
						}
						
					},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
</script>

<style lang="scss">
	.remarkImage {
	    width: 558rpx;
	    margin-left: 20rpx;
		display: flex;
	    flex-wrap: wrap;
	    .item_image {
	        position: relative;
	        width: 160rpx;
			height: 160rpx;
	        margin-right: 10rpx;
	        margin-top: 10rpx;
	        .image {
	            width: 160rpx;
	            height: 160rpx;
	            border-radius: 10rpx;
	            object-fit: cover;
	        }
	    }
	}
.background {
	// background: #F6F6F6;
}
.details_list {
	// margin-bottom: 20rpx;
	border-bottom: 20rpx solid #F6F6F6;
	.details_item {
		display: flex;
		align-items: flex-start;
		width: 100%;
		padding: 20rpx 30rpx;
		background: $color-f;
		 box-sizing: border-box;
		 border-bottom: 2rpx solid #ECECEC;
		.details_item_list {
			display: flex;
			flex-direction: row;
			font-size: 28rpx;
			height: 50rpx;
			line-height: 50rpx;
			margin-bottom: 20rpx;
			.title {
				font-weight: 400;
				color: $color-6;
				margin-right: 40rpx;
			}
			.value {
				font-weight: 500;
				color: $color-3;
			}
		}
	}
}
.details_note {
	background: $color-f;
	padding: 20rpx 30rpx;
	display: flex;
	flex-direction: row;
	font-size: 28rpx;
	.title {
		font-weight: 400;
		color: $color-6;
		margin-right: 40rpx;
	}
	.value {
		font-weight: 500;
		color: $color-3;
	}
}

</style>
